html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

img {
  width: 100%;
  height: 100%;
}

main {
  background-color: #edf2f4;
  padding: 2.5rem 20rem;
  text-align: center;
  color: #FFFFFF;
  height: 140rem;
}
main .headers {
  backdrop-filter: blur(12px) saturate(180%);
  -webkit-backdrop-filter: blur(12px) saturate(180%);
  background-color: #073b4c;
  border-radius: 1rem;
  padding: 4rem 0;
  margin-bottom: 2rem;
}
main .headers .titulo {
  font-size: 6rem;
  text-transform: uppercase;
  margin-bottom: 1rem;
  margin-top: 1rem;
}
main .headers .subtitulo {
  margin-bottom: 4rem;
}
main .headers .grupo {
  font-size: 2rem;
}
main .headers .integrantes {
  font-size: 2rem;
}
main .headers .integrantes span {
  text-transform: uppercase;
  font-weight: bold;
}
main .menu {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 4rem;
}
main .menu .option {
  display: grid;
  grid-template-rows: 25rem 40rem;
  gap: 4rem;
  background-color: #62676C;
  padding: 4rem 2rem;
  border-radius: 2rem;
}
main .menu .option p {
  font-size: 2.8rem;
}
main .menu .option .detalles {
  font-size: 1.8rem;
}
main .menu .option .infoo {
  display: grid;
  grid-template-rows: 12rem 25rem;
  grid-row: 2/3;
}
main .menu .option:nth-child(1) {
  background-color: #ffd166;
}
main .menu .option:nth-child(2) {
  background-color: #06d6a0;
}
main .menu .option:nth-child(3) {
  background-color: #118ab2;
}
main .menu .option .imagenContainer {
  display: flex;
  align-items: center;
  grid-row: 1/2;
}
main .menu .option .imgCircle {
  border-radius: 50%;
  background-color: #FFFFFF;
  box-shadow: 0px 20px 20px rgba(255, 255, 255, 0.2);
}
main .menu .option .imgCircle img {
  transition: transform 0.3s ease;
}
main .menu .option .imgCircle:hover img {
  transform: scale(1.2);
  cursor: pointer;
}

.main__pulso {
  background-color: #ffd166;
  color: #111111;
}
.main__pulso .titulo {
  font-size: 4.2rem;
  color: #14213d;
}
.main__pulso .imgContainer {
  width: 40rem;
  margin: 10rem auto 10rem auto;
  animation: latir 3s infinite;
}
.main__pulso .imgContainer img {
  display: block;
  width: 100%;
  height: auto;
}
.main__pulso #startButton {
  margin-top: 4rem;
}
.main__pulso .valorDelPic {
  background-color: #FFFFFF;
  font-weight: bold;
  width: 12.5rem;
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  padding: 2rem 0;
  margin-top: -30rem;
  margin-left: 50rem;
  position: relative;
}
.main__pulso .instruccionPulso {
  margin-top: 20rem;
  font-size: 2rem;
  padding: 2rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #80ed99;
  border-radius: 2rem;
}

button {
  position: relative;
  border: none;
  background: transparent;
  padding: 0;
  cursor: pointer;
  outline-offset: 4px;
  transition: filter 250ms;
  user-select: none;
  touch-action: manipulation;
}

.shadow {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 12px;
  background: hsla(0, 0%, 0%, 0.25);
  will-change: transform;
  transform: translateY(2px);
  transition: transform 600ms cubic-bezier(0.3, 0.7, 0.4, 1);
}

.edge {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 12px;
  background: linear-gradient(to left, hsl(340, 100%, 16%) 0%, hsl(340, 100%, 32%) 8%, hsl(340, 100%, 32%) 92%, hsl(340, 100%, 16%) 100%);
}

.front {
  display: block;
  position: relative;
  padding: 12px 27px;
  border-radius: 12px;
  font-size: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  color: white;
  background-color: #ef476f;
  will-change: transform;
  transform: translateY(-4px);
  transition: transform 600ms cubic-bezier(0.3, 0.7, 0.4, 1);
  width: 18rem;
  height: 5rem;
}

button:hover {
  filter: brightness(110%);
}

button:hover .front {
  transform: translateY(-6px);
  transition: transform 250ms cubic-bezier(0.3, 0.7, 0.4, 1.5);
}

button:active .front {
  transform: translateY(-2px);
  transition: transform 34ms;
}

button:hover .shadow {
  transform: translateY(4px);
  transition: transform 250ms cubic-bezier(0.3, 0.7, 0.4, 1.5);
}

button:active .shadow {
  transform: translateY(1px);
  transition: transform 34ms;
}

button:focus:not(:focus-visible) {
  outline: none;
}

@keyframes latir {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
